  font-size: var(--fs-400);
  font-weight: var(--fw-700);
  text-transform: uppercase;
  text-decoration: none;
}

.nav-traditional {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 100%;
  height: 100%;
  position: relative;
}
@media (max-width: 1120px) {
  .nav-traditional {
    display: none;
  }
}
.nav-traditional ul {
  margin: 0;
  list-style: none;
}
.nav-traditional > ul {
  display: flex;
  justify-content: flex-end;
  gap: min(2.5em, 5vw);
  height: 100%;
}
.nav-traditional > ul > li {
  display: flex;
  align-items: center;
  position: relative;
}
.nav-traditional > ul > li > a {
  display: inline-block;
  width: fit-content;
  padding-block: 2.3em;
  transition: all var(--t-medium);
}
.nav-traditional > ul > li > a:after {
  content: "";
  display: block;
  border-bottom: 2px solid var(--c-primary);
  transform: scaleX(0);
  transform-origin: 0% 50%;
  transition: transform var(--t-fast);
}
.nav-traditional > ul > li > a:hover:after, .nav-traditional > ul > li > a:focus:after {
  transform: scaleX(1);
}
.nav-traditional > ul > li.nav-hide {
  display: none;
}
.nav-traditional > ul > li > ul {
  display: none;
  position: absolute;
  inset: var(--nav-dropdown-top) auto auto 0;
  white-space: nowrap;
}
.nav-traditional > ul > li > ul > li {
  background-color: var(--c-white);
  border-bottom: 1px solid var(--c-grey-100);
}
.nav-traditional > ul > li > ul > li > a {
  display: block;
  width: 100%;
  padding: 1em;
  transition: all var(--t-fast);
}
.nav-traditional > ul > li > ul > li > a:hover, .nav-traditional > ul > li > ul > li > a:focus {
  background-color: var(--c-grey-100);
}
.nav-traditional > ul > li:hover > a:after, .nav-traditional > ul > li:focus > a:after, .nav-traditional > ul > li.is-focused > a:after {
  transform: scaleX(1);
}
.nav-traditional > ul > li:hover > ul, .nav-traditional > ul > li:focus > ul, .nav-traditional > ul > li.is-focused > ul {
  display: flex;
  flex-direction: column;
  z-index: 1;
}

.header__nav {
