.overlay-nav-toggle {
  width: 45px;
  height: 45px;
  z-index: 40000;
  position: relative;
}

.menu-hamburger {
  width: 30px;
  height: 30px;
  position: absolute;
  inset: 50% auto auto 50%;
  transform: translate(-50%, -50%);
}
.menu-hamburger .line-top,
.menu-hamburger .line-middle,
.menu-hamburger .line-bottom {
  transition: all 0.5s;
  transform-origin: center center;
  stroke: var(--c-primary);
}
.menu-hamburger .line-top {
  transform: translateY(1px);
}
.menu-hamburger .line-middle {
  transform: translateY(10px);
}
.menu-hamburger .line-bottom {
  transform: translateY(19px);
}

.overlay-nav-toggle.overlay-nav--visible .menu-hamburger .line-middle {
  opacity: 0;
}
.overlay-nav-toggle.overlay-nav--visible .menu-hamburger .line-top {
  transform: rotate(45deg) translateY(10px);
  stroke: white !important;
}
.overlay-nav-toggle.overlay-nav--visible .menu-hamburger .line-bottom {
  transform: rotate(-45deg) translateY(10px);
  stroke: white !important;
}
.overlay-nav-toggle.overlay-nav--visible:hover .line-top,
.overlay-nav-toggle.overlay-nav--visible:hover .line-bottom, .overlay-nav-toggle.overlay-nav--visible:focus .line-top,
.overlay-nav-toggle.overlay-nav--visible:focus .line-bottom {
  stroke: var(--c-primary) !important;
}

.overlay-nav nav > ul > li > a {
