.overlay-nav nav > ul > li > a .dropdown-toggle {
  position: absolute;
  right: 1px;
  top: 50%;
  transform: translateY(-50%);
  width: 44px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: var(--c-grey-100);
  transition: transform var(--t-medium);
}
.overlay-nav nav > ul > li > a .dropdown-toggle:before {
