}
.overlay-nav nav > ul > li > a .dropdown-toggle.show {
  transform: translateY(-50%) rotate(90deg);
}
.overlay-nav nav > ul > li > a .dropdown-toggle:is(:hover, :focus) {
  color: var(--c-white);
}
.overlay-nav nav > ul > li.nav-hide {
