}
.overlay-nav nav > ul > li > ul {
  list-style: none;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: all var(--t-medium);
}
.overlay-nav nav > ul > li > ul.show {
  max-height: 100%;
  opacity: 1;
}
.overlay-nav nav > ul > li > ul > li > a {
  display: flex;
  justify-content: center;
  padding: 1em;
  border-bottom: 1px solid var(--c-grey-400);
  transition: all var(--t-medium);
}
.overlay-nav nav > ul > li > ul > li > a:is(:hover, :focus) {
